.DiscountCoupon_container{
    padding: 0px 40px 80px 40px;
    display: flex;
    width: 1440px;
    align-items: flex-start;
    align-content: flex-start;
    gap: 32px;
    flex-wrap: wrap;
    flex-direction: row;
}

.DiscountCoupon_banner{
    display: inline-flex;
    padding: 32px 32px 0px 32px;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    border-radius: var(--radius-big, 12px);
    background: linear-gradient(261deg, #0B710B 32.63%, #393 98.96%);
}

.DiscountCoupon_name{
    align-self: stretch;
    color: var(--txt-white, #FFF);
    text-align: center;
    font-family: Montserrat;
    font-size: 64px;
    font-style: normal;
    font-weight: 700;
    line-height: 110%; /* 70.4px */
}

.DiscountCoupon_body{
    display: flex;
    align-items: center;
    gap: 32px;
}

.DiscountCoupon_IMG{
    width: 748px;
    height: 360px;
    background-image: url('../../media/imagediscount.png');
}

.Registration_form{
    display: flex;
    width: 516px;
    flex-direction: column;
    align-items: flex-start;
    gap: 32px;
}

.warning_text{
    position: absolute;
    color: rgb(90, 13, 13);
    font-size: 15px;
}

@media (max-width: 400px) {
    .DiscountCoupon_container {
    padding: 0;
    width: 90%;
    align-items: center;
    align-content: center;
    gap: 16px;
    flex-wrap: nowrap;
    flex-direction: column;
    }
    
    .DiscountCoupon_banner {
        width: 90%;
    padding: 16px;
    border-radius: 8px;
    }
    
    .DiscountCoupon_name {
    font-size: 32px;
    line-height: 100%;
    }
    
    .DiscountCoupon_body {
        width: 90%;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    }
    
    .DiscountCoupon_IMG {
    width: 0;
    height: 0;
    }
    
    .Registration_form {
    width: 100%;
    align-items: center;
    }
    }