

.CategoriesItem_card{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
    flex-shrink: 0;
}

.CategoriesItem_IMG{
    height: 350px;
    align-self: stretch;
    border-radius: var(--radius-big, 12px);
}

.CategoriesItem_Name{
    text-decoration: none;
    align-self: stretch;
    color: var(--txt-black, #282828);
    text-align: center;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 130%; /* 26px */
}

@media (max-width: 600px) {
    .CategoriesItem_card {
        gap: 8px;
    }

    .CategoriesItem_IMG {
        height: 250px;
        border-radius: var(--radius-medium, 8px);
    }

    .CategoriesItem_Name {
        font-size: 16px;
        line-height: 120%; /* 19.2px */
    }
}