.CategoriesList_bar{
    display: flex;
    width: 1368px;
    align-items: flex-start;
    align-content: flex-start;
    gap: 32px;
    flex-wrap: wrap;
}

@media (max-width: 768px) {
    .CategoriesList_bar {
        width: 100%;
        justify-content: center;
        gap: 16px;
        flex-direction: column;
    }
}