
  h1{
    font-weight: 700;
    font-size: 96px;
  }
  h2{
    font-weight: 700;
    font-size: 64px;
  }
  p{
    font-weight: 500;
    font-size: 20px;
  }

.footer{
    width: 1360px;
    display: inline-flex;
    padding: 0px 40px 80px 40px;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    gap: 40px;
}

.info_bar_field{
    display: flex;
    flex-direction: row;
    width: 1380px;
}

.info_bar_big{
    display: flex;
    padding: 32px;
    width: 716px;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    align-self: stretch;
    border-radius: var(--radius-big, 12px);
    background: var(--light-grey, #F1F3F4);
}

.info_bar_small{
    display: flex;
    width: 484px;
    padding: 32px;
    margin-left: 32px;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    align-self: stretch;
    border-radius: var(--radius-big, 12px);
    background: var(--light-grey, #F1F3F4);
}

.info_title{
    margin: 0px;
    font-family: 'Montserrat', sans-serif;
    color: var(--txt-grey, #8B8B8B);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 130%; /* 26px */
}

.info_text{
    margin: 0px;
    font-family: 'Montserrat', sans-serif;
    color: var(--txt-black, #282828);
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: 110%; /* 44px */
}

.map_space{
    height: 350px;
    align-self: stretch;
    border-radius: var(--radius-big, 12px);
}

@media (max-width: 400px) {
  .footer {
  width: 90%;
  padding: 0px;
  justify-content: center;
  align-items: center;
  }
  
  .info_bar_field {
  width: 90%;
  flex-direction: column;
  }
  
  .info_bar_big {
  width: 90%;
  padding: 20px;
  align-items: center;
  }
  
  .info_bar_small {
  width: 90%;
  padding: 20px;
  margin-left: 0;
  gap: 12px;
  }
  
  .info_text {
  font-size: 24px;
  line-height: 130%;
  }
  
  .map_space {
  height: 250px;
  }
  }